@import '~vue2-dropzone/dist/vue2Dropzone.min.css';
html {
  -webkit-font-smoothing: antialiased;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
body {
  color: #818181;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  line-height: 1.4;
  overflow-x: hidden;
  position: relative;
}
* {
  box-sizing: border-box;
  outline: none;
}
a {
  text-decoration: none;
}
pre {
  border: 1px solid #f4f4f4;
  background-color: #f0f0f0;
  margin: 50px;
  padding: 50px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #000000;
  cursor: default;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  line-height: 1;
  margin: 1em 0 1.5em;
  text-transform: uppercase;
}
h1 {
  font-size: 1.75rem;
  margin-bottom: 2rem;
  margin-top: 0;
  padding-bottom: 2.25rem;
  position: relative;
  text-align: center;
}
@media screen and (min-width: 992px) {
  h1 {
    font-size: 2.75rem;
    margin-bottom: 4rem;
    padding-bottom: 2.25rem;
  }
}
h1:after {
  background-color: #FF0000;
  border-radius: 2rem;
  bottom: 0;
  content: '';
  display: block;
  height: 4px;
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
  width: 90px;
}
@media screen and (min-width: 992px) {
  h1:after {
    width: 176px;
  }
}
h2 {
  font-size: 2rem;
}
@media screen and (min-width: 992px) {
  h2 {
    font-size: 3rem;
  }
}
h3 {
  font-size: 1.5rem;
}
h4 {
  font-size: 1.25rem;
}
h5 {
  font-size: 1.125rem;
}
h6 {
  font-size: 1.125rem;
}
section {
  padding-bottom: 4rem;
  padding-top: 4rem;
}
@media screen and (min-width: 992px) {
  section {
    padding-bottom: 7rem;
    padding-top: 7rem;
  }
}
/**** Botones ****/
.btn {
  border: none;
  border-radius: 2rem;
  display: inline-block;
  font-size: 0.75rem;
  letter-spacing: 1px;
  padding: 0.75rem 1rem;
  text-align: center;
  text-transform: uppercase;
}
.btn.btn-red {
  background-color: #FF0000;
  color: #FFFFFF;
}
.btn.btn-outline-white {
  background-color: transparent;
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  padding: calc(0.75rem - 1px) calc(1rem - 1px);
}
.btn.btn-ghost {
  color: #818181;
  font-size: 0.875rem;
  letter-spacing: 0;
  padding: 0.25rem;
  text-transform: none;
}
#main.camel-background {
  background-color: #FBFAF8;
  min-height: 100vh;
  padding-top: 150px;
}
.page-text {
  padding-bottom: 2rem;
  padding-top: 0rem;
  text-align: justify;
}
.page-text a {
  color: #FF0000;
}
.page-text ol li {
  margin-bottom: 0.5rem;
}
#header {
  left: 0;
  padding: 1rem 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 3;
}
#header.header-white {
  background-color: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
}
#header.header-white .logo img.black {
  display: block;
}
#header.header-white .logo img.white {
  display: none;
}
#header.header-white #main-nav .main-nav-box .main-nav li a {
  color: #000000;
}
#header.header-white #main-nav .main-nav-box .login-nav .btn-outline-white {
  border-color: #000000;
  color: #000000;
}
#header .logo {
  width: 120px;
}
#header .logo img {
  height: auto;
  width: 100%;
}
#header .logo img.black {
  display: none;
}
#header .logo img.white {
  display: block;
}
#header #main-nav .main-nav-button {
  align-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  display: flex;
  flex-wrap: wrap;
  height: 60px;
  justify-content: center;
  margin-left: auto;
  padding: 1rem;
  position: relative;
  width: 60px;
  z-index: 5;
}
@media screen and (min-width: 992px) {
  #header #main-nav .main-nav-button {
    display: none;
  }
}
#header #main-nav .main-nav-button:focus,
#header #main-nav .main-nav-button:visited {
  border: none;
  outline: none;
}
#header #main-nav .main-nav-button span {
  background-color: #FFFFFF;
  border-radius: 1rem;
  display: block;
  flex: 0 0 auto;
  height: 3px;
  margin-bottom: 0.25rem;
  transition: all 0.4s ease;
  transform-origin: 0 0;
  width: 1.5rem;
}
#header #main-nav .main-nav-button span:last-child {
  margin-bottom: 0;
}
#header #main-nav .main-nav-button.active span:first-child {
  transform: rotate(45deg) translate(3px, -4px);
}
#header #main-nav .main-nav-button.active span:nth-child(2) {
  opacity: 0;
}
#header #main-nav .main-nav-button.active span:last-child {
  transform: rotate(-45deg) translate(0px, 3px);
}
#header #main-nav .main-nav-box {
  align-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.95);
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  left: 0;
  opacity: 0;
  padding: 5.75rem 2rem 2rem;
  position: fixed;
  top: 0;
  transition: 0.4s all ease;
  visibility: hidden;
  width: 100%;
  z-index: 4;
}
@media screen and (min-width: 992px) {
  #header #main-nav .main-nav-box {
    background-color: transparent;
    height: auto;
    justify-content: flex-end;
    left: auto;
    opacity: 1;
    padding: 0;
    position: relative;
    top: auto;
    visibility: initial;
    width: auto;
  }
}
#header #main-nav .main-nav-box.active {
  opacity: 1;
  visibility: visible;
}
#header #main-nav .main-nav-box .main-nav {
  flex: 0 0 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 992px) {
  #header #main-nav .main-nav-box .main-nav {
    display: flex;
    flex: 0 0 auto;
    justify-content: flex-end;
  }
}
#header #main-nav .main-nav-box .main-nav li {
  text-align: center;
}
#header #main-nav .main-nav-box .main-nav li a {
  color: #FFFFFF;
  display: block;
  font-size: 0.875rem;
  padding: 0.75rem 1rem;
  text-align: center;
  text-transform: uppercase;
}
@media screen and (min-width: 992px) {
