    margin-left: 2rem;
  }
}
#header #main-nav .main-nav-box .login-nav .btn {
  display: block;
  margin-top: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
  text-transform: uppercase;
}
@media screen and (min-width: 992px) {
  #header #main-nav .main-nav-box .login-nav .btn {
    display: inline-block;
    margin-left: 1rem;
    margin-top: 0;
  }
  #header #main-nav .main-nav-box .login-nav .btn:first-child {
    margin-left: 0;
  }
}
#header #main-nav .main-nav-box .login-nav .btn-logout {
  display: inline-block;
  margin-left: 1rem;
  position: relative;
  vertical-align: top;
}
#header #main-nav .main-nav-box .login-nav .btn-logout:before {
  background-color: #818181;
  border-radius: 1rem;
  content: '';
