  height: 20px;
  left: 0;
  position: absolute;
  width: 2px;
}
#header #main-nav .main-nav-box .login-nav .btn-logout img {
  display: block;
  height: auto;
  width: 20px;
}
#main-banner {
  position: relative;
}
#main-banner .item {
  align-content: center;
  align-items: center;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  height: 100vh;
  position: relative;
}
#main-banner .item:after {
  background-color: rgba(0, 0, 0, 0.7);
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
#main-banner .item h1 {
  color: #FFFFFF;
  flex: 0 0 100%;
  font-size: 2rem;
  font-weight: 600;
  margin: 0 0 0.5rem;
  padding: 0;
  position: relative;
  text-align: center;
  z-index: 2;
}
#main-banner .item h1::after {
  content: none;
}
@media screen and (min-width: 992px) {
  #main-banner .item h1 {
    font-size: 3rem;
  }
}
#main-banner .item h2 {
  color: #FFFFFF;
  flex: 0 0 100%;
  font-size: 1rem;
  font-weight: 300;
  margin: 0;
  position: relative;
  text-align: center;
  z-index: 2;
}
@media screen and (min-width: 992px) {
  #main-banner .item h2 {
    font-size: 1.5rem;
  }
}
#main-banner .mouse-wheel {
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: bounce-2;
  animation-timing-function: ease;
  bottom: 1rem;
  height: auto;
  left: 50%;
  position: absolute;
  transform: transalte(-50%, 0);
  width: 22px;
  z-index: 2;
}
#main-banner .mouse-wheel img {
  display: block;
  height: auto;
  width: 100%;
}
@keyframes bounce-2 {
  0% {
    transform: translate(-50%, 0);
  }
  50% {
    transform: translate(-50%, -20px);
  }
  100% {
    transform: translate(-50%, 0);
  }
}
